// React and Next
import { headers } from "next/headers";
import { notFound } from "next/navigation";

// API
import {
  getProductSeo,
  getbuyerproduct,
  getbuyerproductcount,
  getfilter,
} from "@/api/serversideaction/productdetail";
import { getcategory, getsubcategory } from "@/api/serversideaction/category";

// Utils
import { GetToken } from "@/utilities/cookies";

// Components
import TopCategoriesServer from "@/Components/TopCategories/server";
import createMetadata from "@/Components/Seo/createMetadata";
import createStructuredData from "@/Components/Seo/createStructuredData";
import StructuredData from "@/Components/Seo/StructuredData";
import Productlistview from "@/Components/Productlistview";

export async function generateMetadata(props) {
  const params = await props.params;
  const h = await headers();
  const host = h.get("host");
  const proto = h.get("x-forwarded-proto") || "https";
  const base = `${proto}://${host}`;

  try {
    const [seoRes, catRes] = await Promise.all([
      getProductSeo(params.slug),
      getsubcategory(params.slug),
    ]);

    const seo = seoRes?.data || {};
    const cat = catRes?.data || {};

    // PRIORITY → SEO → Category → Fallback
    const title = seo.metaTagTitle || cat.name || params.slug || "Category";

    const description =
      seo.metaTagDescription || cat.categoryDescription || `Explore ${title}`;

    const keywords = seo.metaTagKeyword || `${params.slug}, products, category`;

    const image = seo.ogImage
      ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${seo.ogImagePath}&name=${seo.ogImage}&width=1200&height=630`
      : cat.image
      ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${cat.imagePath}&name=${cat.image}&width=1200&height=630`
      : `${base}/img/logo.svg`;

    return createMetadata({
      title,
      description,
      keywords,
      imageUrl: image,
      url: `${base}/category/${params.slug}`,
      baseUrl: base,
      showRobots: true,
    });
  } catch (error) {
    console.error("SEO metadata error:", error);

    return createMetadata({
      title: params.slug,
      description: `Explore ${params.slug}`,
      keywords: params.slug,
      imageUrl: `${base}/img/logo.svg`,
      url: `${base}/category/${params.slug}`,
      baseUrl: base,
      showRobots: true,
    });
  }
}

export default async function Listview(props) {
  const params = await props.params;
  const [
    token,
    product,
    pagecount,
    filter,
    selectedCategory,
    categoryList,
    seoRes,
  ] = await Promise.all([
    GetToken(),
    getbuyerproduct(params?.slug, 0),
    getbuyerproductcount(params?.slug, 0),
    getfilter(params?.slug),
    getsubcategory(params?.slug),
    getcategory(),
    getProductSeo(params.slug),
  ]);

  if (
    product?.message ==
    `Category ${params?.slug} is Not Allowed for this Site..! -- Contact Your Admin`
  ) {
    return notFound();
  }

  // PRIORITY → SEO → Category → Fallback
  const seo = seoRes?.data || {};
  const cat = selectedCategory?.data || {};

  const title = seo.metaTagTitle || cat.name || params.slug;

  const description =
    seo.metaTagDescription || cat.categoryDescription || `Explore ${title}`;

  const h = await headers();
  const host = h.get("host");
  const proto = h.get("x-forwarded-proto") || "https";
  const base = `${proto}://${host}`;

  const image = seo.ogImage
    ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${seo.ogImagePath}&name=${seo.ogImage}&width=1200&height=630`
    : cat.image
    ? `${process.env.NEXT_PUBLIC_IMAGE_URL}?path=${cat.imagePath}&name=${cat.image}&width=1200&height=630`
    : `${base}/img/logo.svg`;

  // FINAL STRUCTURED DATA
  const schema = createStructuredData({
    title,
    description,
    url: `${base}/category/${params.slug}`,
    image,
    dateCreated: cat?.createdDate || null,
    dateModified: cat?.modifiedDate || null,
    organizationName: host,
    organizationLogo: image,
    organizationUrl: base,
  });

  return (
    <>
      <TopCategoriesServer />

      <StructuredData data={schema} />

      <Productlistview
        token={token}
        params={params}
        pagecount={pagecount}
        filter={filter}
        selectedCategory={selectedCategory?.data}
        categoryList={categoryList?.data}
      />
    </>
  );
}
